import Router from 'vue-router'
import Vue from 'vue'
const Home = ()=> import('../views/home/Home')
const Category = ()=> import('../views/category/Category')
const Cart = ()=> import('../views/cart/Cart')
const Profile = ()=> import('../views/profile/Profile')
const Detail = ()=> import('../views/detail/Detail')
Vue.use(Router)

export default new Router ({
    routes:[
        
        {
            path:'/',
            redirect:'/home'
        }
        ,{
        path:'/home',
        name:'Home',
        component:Home
    },{
        path:'/category',
        name:'Category',
        component:Category
    },
    {
        path:'/cart',
        name:'Cart',
        component:Cart
    },
    {
        path:'/profile',
        name:'Profile',
        component:Profile
    },
    {
        path:'/detail/:iid',
        name:'Detail',
        component:Detail
    }
],
mode:'hash'
})