<template>
    <div class="tab-bar-item" @click='itemClick'>
       <div v-if="!isActive" class='item-icon'> <slot name="item-icon"> </slot></div>
       <div v-else class="item-icon"><slot name="item-active-icon"></slot> </div>
        <div class="item-text"><slot name='item-text'></slot> </div>
    </div>
</template>
<script>
export default {
    name:'TabBarItem',
    props: {
        path: {
            type:String,
            required:true
        }
    },
    methods: {
        itemClick() {
            this.$router.replace(this.path);
          

        }
    },
    computed: {
        isActive() {
            return this.$route.path == this.path;
        }
    }
}
</script>

<style scoped>
 .tab-bar-item {
     flex: 1;
 }

 .item-icon img {
     width: 24px;
     height: 24px;
 }
</style>